@use '../helpers' as h;

.c-btn {
}

.button{
    font-family: h.$font-family;
    border-radius: 30px;
    color: #fff;
    font-size: 21px;
    font-weight: 700;
    line-height: 100%;
    outline: none;
    padding: 19px 75px 14px 31px;
    position: relative;
    text-transform: uppercase;
    background-color: h.$accent-color;
    position: relative;
    margin: 0 auto;
    display: block;
    transition: color ease 0.3s;
    &:after{
        content: "";
            position: absolute;
            height: 20px;
            right: 23px;
            top: 18px;
            width: 20px;
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url('data:image/svg+xml,<svg width="20" height="16" viewBox="0 0 20 16" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 7C0.447715 7 0 7.44772 0 8C0 8.55228 0.447715 9 1 9V7ZM19.7071 8.70711C20.0976 8.31658 20.0976 7.68342 19.7071 7.29289L13.3431 0.928932C12.9526 0.538408 12.3195 0.538408 11.9289 0.928932C11.5384 1.31946 11.5384 1.95262 11.9289 2.34315L17.5858 8L11.9289 13.6569C11.5384 14.0474 11.5384 14.6805 11.9289 15.0711C12.3195 15.4616 12.9526 15.4616 13.3431 15.0711L19.7071 8.70711ZM1 9H19V7H1V9Z" fill="%23FFFFFF"/></svg>'); 
    }
    &:hover{
        background-color: #fff;
        color: h.$accent-color;
        &:after{
            content: "";               
                background-image: url('data:image/svg+xml,<svg width="20" height="16" viewBox="0 0 20 16" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M1 7C0.447715 7 0 7.44772 0 8C0 8.55228 0.447715 9 1 9V7ZM19.7071 8.70711C20.0976 8.31658 20.0976 7.68342 19.7071 7.29289L13.3431 0.928932C12.9526 0.538408 12.3195 0.538408 11.9289 0.928932C11.5384 1.31946 11.5384 1.95262 11.9289 2.34315L17.5858 8L11.9289 13.6569C11.5384 14.0474 11.5384 14.6805 11.9289 15.0711C12.3195 15.4616 12.9526 15.4616 13.3431 15.0711L19.7071 8.70711ZM1 9H19V7H1V9Z" fill="%2312A1DC"/></svg>'); 
        }
    }
    // width: 245px;
}

