/* header */
.header {
  width: 100%;
  z-index: 3;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 25px 20px 25px;
  position: fixed;
  margin-bottom: 7px;
  background-color: #FFF;
  transform: translateY(0%);
  animation: slideOut 0.5s ease forwards;
}

@keyframes slideOut {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0%);
  }
}
.logo-place {
  width: 154px;
