.footer {
  background-color: #035DA2;
  color: #fff;
  padding: 83px 15px 45px;
  margin-top: 120px;
}

.links-block {
  margin-bottom: 29px;
}

.footer-container {
  display: flex;
  flex-direction: column;
}

.links-header {
  font-family: "Circe", sans-serif;
  font-size: 24px;
  font-weight: 400;
  margin-bottom: 5px;
}

.links-list {
  padding-left: 0px;
}

