  font-family: "Circe", sans-serif;
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 5px;
}
.links_li .contacts-img {
  max-width: 27px;
  margin-inline-end: 19px;
}

.contacts-li {
  display: flex;
}
.contacts-li .links_a {
  font-size: 17px;
}

.links_a {
  position: relative;
}
.links_a:after {
  content: "";
  display: block;
  position: absolute;
  left: 0px;
  bottom: -1px;
  height: 1px;
  width: 0px;
  background-color: #fff;
  transition: all ease 0.5s;
}
.links_a:hover:after {
  width: 100%;
}

.copy-block {
  border-top: 1px solid rgba(249, 249, 249, 0.5);
  padding-top: 35px;
}
.copy-block .copy {
  color: rgba(250, 250, 250, 0.5);
  font-size: 16px;
  max-width: 700px;
}

